import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { QuestionBase } from '../../models/question.interface';
@Component({
  selector: 'app-radiobutton',
  template: `
  <div class='demo-full-width margin-top' [formGroup]='group'>
    <label class='radio-label-padding'>{{question.label}}:</label><br/>
    <mat-radio-group [formControlName]='question.name'>
    <mat-radio-button *ngFor='let item of question.responses' [value]='item.response' style="padding-right:10px;">
      {{item.response}}
    </mat-radio-button>
  </mat-radio-group>
  </div>
`,
  styleUrls: ['./radiobutton.css']
})
export class RadiobuttonComponent implements OnInit {
  question: QuestionBase;
  group: FormGroup;
  constructor() {}
  ngOnInit() {}
}
