import { Injectable, EventEmitter } from '@angular/core';
import { Validators } from '@angular/forms';
import { QuestionBase , Validator} from '../models/question.interface';
import { Http, Headers, Response } from '@angular/http';
import { Observable, of, BehaviorSubject } from 'rxjs';
import { catchError, retry, map, tap } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class QuestionService {
  questions: QuestionBase[];
  formUrl = '../../assets/form.json';
  data: any;
  public dataRead = new BehaviorSubject<boolean>(false);
  constructor(private http: Http) {
  }

  get getQuestions(): Observable<QuestionBase[]> {

    return this.http.get(this.formUrl)
      .pipe(
        map((response: Response) => response.json()),
        map((data: any) => {
          let questions: QuestionBase[] = [];
          let response = data.$schema.questions;

          response.forEach((res: any) => {       
            let quest = {
              controlType: res.controlType,
              label: res.label,
              inputType: res.inputType,
              name: res.name,
              options: res.options,
              value: res.value,
              validations: res.validations,
              order: res.order
            };
            questions.push(quest);
          });

          this.dataRead.next(true);
          return questions.sort((a, b) => a.order - b.order);
        }),
        catchError(this.handleError('getQuestions', [])));
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.log(`${operation} failed: ${error}`);
      return of(result as T);
    };
  }
}
