import { Component, ViewChild, OnInit } from '@angular/core';
import { QuestionBase } from './models/question.interface';
import { DynamicFormComponent } from './components/dynamic-form/dynamic-form.component';
import { QuestionService } from './services/question.service';
import { Observable, Subject, BehaviorSubject } from 'rxjs';
// import { Component } from '@angular/core';
// import { Platform } from 'ionic-angular';
// import { StatusBar } from '@ionic-native/status-bar';
// import { SplashScreen } from '@ionic-native/splash-screen';

// import { HomePage } from '../pages/home/home';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  @ViewChild(DynamicFormComponent) form: DynamicFormComponent;
  questionsConfig: BehaviorSubject<QuestionBase[]>;
  title = 'iMonitor';

  constructor(private questService: QuestionService) {
    this.questionsConfig = new BehaviorSubject<QuestionBase[]>([]);
  }

  ngOnInit() {
    this.getQuestions().subscribe((res: any) => {
      this.questionsConfig.next(res);
    });
  }

  getQuestions() {
    return this.questService.getQuestions;
  }

  onSubmit(event: Event) {
    console.log(JSON.stringify(this.form.value));
  }
}


