import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { QuestionBase } from '../../models/question.interface';
@Component({
selector: 'app-date',
template: `
<mat-form-field class="demo-full-width margin-top" [formGroup]="group">
<input matInput [matDatepicker]="picker" [formControlName]="question.name" [placeholder]="question.label">
<mat-datepicker-toggle matSuffix [for]="picker"></mat-datepicker-toggle>
<mat-datepicker #picker></mat-datepicker>
<mat-hint></mat-hint>
<ng-container *ngFor="let validation of question.validations;" ngProjectAs="mat-error">
<mat-error *ngIf="group.get(question.name).hasError(validation.name)">{{validation.message}}</mat-error>
</ng-container>
</mat-form-field>
`,
styles: []
})
export class DateComponent implements OnInit {
question: QuestionBase;
group: FormGroup;
constructor() {}
ngOnInit() {}
}
