import {
  ComponentFactoryResolver, ComponentRef, Directive, Input, OnInit,
  ViewContainerRef
} from '@angular/core';
import { FormGroup } from '@angular/forms';
import { QuestionBase } from '../../models/question.interface';
import { InputComponent } from '../../components/input/input.component';
import { ButtonComponent } from '../../components/button/button.component';
import { SelectComponent } from '../../components/select/select.component';
import { DateComponent } from '../../components/date/date.component';
import { RadiobuttonComponent } from '../../components/radiobutton/radiobutton.component';
import { CheckboxComponent } from '../../components/checkbox/checkbox.component';

const componentMapper = {
  input: InputComponent,
  button: ButtonComponent,
  select: SelectComponent,
  date: DateComponent,
  radiobutton: RadiobuttonComponent,
  checkbox: CheckboxComponent
};

@Directive({
  selector: '[appDynamicField]'
})
export class DynamicFieldDirective implements OnInit {
  @Input() question: QuestionBase;
  @Input() group: FormGroup;
  componentRef: any;
  constructor(private resolver: ComponentFactoryResolver, private container: ViewContainerRef) { }

  ngOnInit() {
    const factory = this.resolver.resolveComponentFactory(
      componentMapper[this.question.controlType]
    );
    this.componentRef = this.container.createComponent(factory);
    this.componentRef.instance.question = this.question;
    this.componentRef.instance.group = this.group;
  }

}

