import { Injectable } from '@angular/core';
import { FormBuilder, Validators, FormControl } from '@angular/forms';
import { QuestionBase } from '../models/question.interface';
@Injectable({
  providedIn: 'root'
})
export class FormControlService {
  constructor() { }

  createControl(questions: QuestionBase[], fb: FormBuilder) {  
    const group = fb.group({});
    questions.forEach(question => {
      if (question.controlType === 'button') {
        return;
      }
      const control = fb.control(
        question.value,
        this.bindValidations(question.validations || [])
      );
      group.addControl(question.name, control);
    });
    return group;
  }

  bindValidations(validations: any) {
    if (validations.length > 0) {
      const validList = [];

      validations.forEach((valid: any) => {
        if (valid.validator.indexOf('required') >= 0) {
          valid.validator = Validators.required;
        } else if (valid.validator.indexOf('pattern') >= 0) {
          valid.validator = Validators.pattern;
        }
        else if (valid.validator.indexOf('email') >= 0) {
          valid.validator = Validators.email;
        } else if (valid.validator.indexOf('min') >= 0) {
          valid.validator = Validators.min;
        }
        else if (valid.validator.indexOf('max') >= 0) {
          valid.validator = Validators.max;
        }
        else if (valid.validator.indexOf('minLength') >= 0) {
          valid.validator = Validators.minLength;
        } else if (valid.validator.indexOf('maxLength') >= 0) {
          valid.validator = Validators.maxLength;
        }
        else if (valid.validator.indexOf('requiredTrue') >= 0) {
          valid.validator = Validators.requiredTrue;
        } else if (valid.validator.indexOf('nullValidator') >= 0) {
          valid.validator = Validators.nullValidator;
        }

        validList.push(valid.validator);
      });
      return Validators.compose(validList);
    }
    return null;
  }
}