import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { QuestionBase } from '../../models/question.interface';
@Component({
selector: 'app-input',
template: `
<mat-form-field class="demo-full-width" [formGroup]="group">
<label [attr.for]="question.name">{{question.label}}</label>

<input matInput [formControlName]="question.name" [placeholder]="question.label" [type]="question.inputType">
<ng-container *ngFor="let validation of question.validations;" ngProjectAs="mat-error">
<mat-error *ngIf="group.get(question.name).hasError(validation.name)">{{validation.message}}</mat-error>
</ng-container>
</mat-form-field>
`,
styles: []
})
export class InputComponent implements OnInit {
question: QuestionBase;
group: FormGroup;
constructor() {}
ngOnInit() {}
}
