import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { QuestionBase, Validator } from '../../models/question.interface';
import { FormControlService } from '../../services/formcontrol.service';
import { QuestionService } from 'src/app/services/question.service';
import { BehaviorSubject } from 'rxjs';
@Component({
  selector: 'app-dynamic-form',
  template: `
    <form class="dynamic-form" [formGroup]="form" (submit)="onSubmit($event)">
      <ng-container *ngFor="let question of questionz;" appDynamicField [question]="question" [group]="form">
      </ng-container>
    </form>`,
  styles: []
})
export class DynamicFormComponent implements OnInit {
  @Input() questions: BehaviorSubject<QuestionBase[]>;
  @Output() submit: EventEmitter<any> = new EventEmitter<any>();
  form: FormGroup;
  payLoad: string;
  questionz: QuestionBase[];
  constructor(private fb: FormBuilder, private fcs: FormControlService, private qs: QuestionService) { }

  ngOnInit() {
    this.questions.subscribe((res)=>{     
      this.questionz = res;
      this.form = this.fcs.createControl(this.questionz, this.fb);
    });   
  }

  get value() {  
    return this.form.value;
  }

  onSubmit(event: Event) {
    event.preventDefault();
    event.stopPropagation();
    if (this.form.valid) {
      this.payLoad = JSON.stringify(this.form.value);
      this.submit.emit(this.form.value);
    } else {
      this.validateAllFormFields(this.form);
    }
  }

  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      control.markAsTouched({ onlySelf: true });
    });
  }

}
